import {
  createRouter,
  createWebHashHistory, 
} from "vue-router";

const routes = [{
    path: "/",
    name: "home",
    component: () => import( /* webpackChunkName: "home" */ "@/views/MainPage.vue")
  },
  {
    path: "/education",
    name: "education",
    component: () => import( /* webpackChunkName: "education" */ "@/views/EducationPage.vue")
  },
  {
    path: "/research",
    name: "research",
    component: () => import( /* webpackChunkName: "research" */ "@/views/ResearchPage.vue")
  },
  {
    path: "/bio",
    name: "bio",
    component: () => import( /* webpackChunkName: "bio" */ "@/components/SkillChart.vue")
  },
  {
    path: "/protfolio",
    name: "protfolio",
    component: () => import( /* webpackChunkName: "protfolio" */ "@/views/PortfolioItem.vue")
  },
  {
    path: "/skill",
    name: "skill",
    component: () => import( /* webpackChunkName: "skill" */ "@/views/SkillPage.vue")
  },
  {
    path: "/contact",
    name: "contact",
    component: () => import( /* webpackChunkName: "contact" */ "@/views/ContactPage.vue")
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'not-found',
    component: () => import( /* webpackChunkName: "404" */ '@/views/NotFound.vue'),
  },
];

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes,
  linkExactActiveClass: "sidebar__nav-item--active",
});

export default router;